				"html": "<p>Closures are functions that refer to independent (free) variables (<code>name</code>), that have been returned by an enclosing function (<code>makeFunc</code>), and can still refer to those variables when they are called (<code>myFunc()</code>). As such, the closure (<code>makeFunc()</code>) 'remembers' the environment in which it was created.</p>\n<pre><code>function makeFunc() {\n  var name = \"Mozilla\";\n  function displayName() {\n    alert(name);\n  }\n  return displayName;\n}\n\nvar myFunc = makeFunc();\nmyFunc();\n</code></pre>\n<p>See <a href=\"https://developer.mozilla.org/en-US/docs/Web/JavaScript/Closures\">MDN</a></p>",
